import React, { Component } from 'react';
import PropTypes from 'prop-types';

import purpleWave from 'assets/images/landing/purple-wave.svg';
import curveArrow from 'assets/images/landing/curve-arrow.svg';
import whyBG from 'assets/images/landing/why-bg.svg';

import SegmentHeader from '../SegmentHeader';


import styles from './ProductionProcess.module.scss';

const data = [{
  title: 'Ideation',
  desc: 'Sometimes you already know what you want, and other times you don’t. In either case, we’re here to help. Send us a bit about your goals, even if you only have vague ideas. We’ll get our experts on it before getting back to you with a dialed and detailed plan of action surrounding the creation of your video. After your approval of the script’s first draft, we’ll go into production.',
}, {
  title: 'Voiceover',
  desc: 'Now comes the fun part. We’ll record the first draft voiceover using a variety of voice types. You can tell us what you liked and what you didn’t before we zero in on the perfect speaking style for you.',
}, {
  title: 'Preview',
  desc: 'We’ll storyboard the script to give you a succinct idea of what the video will look like when finished. This way, there are no surprises and you can rest assured we’re delivering on your vision. You’ll get to check out custom character design, color mood, and other graphic elements coming together in your video. Give us a thumbs-up, and we’ll proceed to...',
}, {
  title: 'Animation',
  desc: 'Let’s bring your story to life by lifting it out of conceptualization and into production! During the animation phase, we take the storyboard and give it a 2D animated life. Custom background music, sound effects, your approved voiceover, and pro pacing give your video the pomp it needs to grab attention.',
}, 
// {
//   title: 'Render',
//   desc: 'It is no secret that Facebook prioritizes video in its. It is no secret that Facebook prioritizes video in its algorithm, allowing you to reach up to 50% more  people at lower costs than other advertisers. While your competitors are still using image ads shouting discounts & offers on Facebook, think about what a video ad with can do for your brand?',
// }
]

class ProductionProcess extends Component {
  renderData(d, index) {
    const width = 100 / data.length;

    return (
      <div
        key={`production-process-${index}`}
        className={styles.dataContainer}
        style={{width: `${width}%`}}
      >
        <div className={styles.dataInner}>
          <h3>{d.title}</h3>
          <hr />
          <p>{d.desc}</p>
          {
            index < data.length - 1 && (
              <div className={styles.curveContainer}>
                <img src={curveArrow} alt="curve-arrow.svg" />
              </div>
            )
          }
        </div>
      </div>
    )
  }

  render() {
    return (
      <div id="process" className={styles.productionProcess}>
        <SegmentHeader
          title="PRODUCTION PROCESS"
          subTitle="PROCESS"
        />
        <div className={styles.purpleWaveContainer}>
          <img src={purpleWave} alt="purple-wave.svg" />
        </div>
        <div className={[styles.dataWrapper, 'main-margin'].join(' ')}>
          {data.map(this.renderData)}
        </div>
        <div className={styles.whyImageContainer}>
          <img src={whyBG} alt="why-bg.svg" />
        </div>
      </div>
    );
  }
}

ProductionProcess.defaultProps = {
};

ProductionProcess.propTypes = {
};

export default ProductionProcess;
