import React, { Component } from 'react';
import { Button } from 'reactstrap';
import PropTypes from 'prop-types';

import Logo from 'assets/images/common/logo.svg';

import styles from './Splash.module.scss';

class Splash extends Component {
  render() {
    const { title } = this.props;

    return (
      <div className={[styles.splashContainer, 'main-margin'].join(' ')}>
        <div className={styles.logoContainer}>
          <img className={styles.mainLogo} src={Logo} alt="INFINTIE FRONTIER" />
          <h1>INFINITE FRONTIER</h1>
        </div>
        <h2>About Us</h2>
        <hr />
        <p className={styles.text}>{`Who — At Infinite Frontier, we’re visual experts and love nothing more than bringing your business into fruition with amazing explainer videos. Our diverse team gives new meaning to the whole is greater than the sum of its parts — we bring our A-game to every client project.

What — Explaining things is hard to do. Even when your business premise, service, or product is simple, telling others about it is complicated. Converting your great ideas into relatable concepts that communicate value is best done visually — which is where we come in.

Mission — We could rest at just making your business appealing. However, we want to present you in the best light possible. To do so, we use an innovative mixture of design talent, a hands-on communicative approach, and an obligation to treat your project as if it were our own. The Infinite Frontier mission is to turn every project we work with into a winner using the power of animated explainer videos. `}</p>
        <div className={styles.buttonContainer}>
          <Button
            onClick={() => {
              try {
                window.FB.CustomerChat.showDialog();
              } catch(error) {
                //eslint-disable-next-line
                console.log(error);
              }
            }}
          >
            <span>ENQUIRE NOW!</span>
            <i className="fas fa-arrow-right" />
          </Button>
        </div>
      </div>
    );
  }
}

Splash.defaultProps = {
  title: '',
};

Splash.propTypes = {
  title: PropTypes.string,
};

export default Splash;
